@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
html, body, #root {
  height: 100%;
}

html {
  overflow: hidden;
}

:root {
  --primary-color: hsl(0, 0%, 100%);
  --secondary-color: hsl(0, 0%, 98%);
  --font-color: hsl(0, 0%, 0%);
}

.dark-theme {
  --primary-color: hsl(210, 22%, 22%);
