.region-selector {
    display: inline-block;
    position: relative;
    box-shadow: 0px 0px 4px 3px rgba(0, 0, 0, 0.02);
    border-radius: 0.25rem;
    overflow: hidden;
}

.region-selector:focus-within {
    box-shadow: 0px 0px 4px 3px rgba(0, 0, 0, 0.1);
}

.region-selector select {
    font-family: "Nunito Sans";
    font-size: 0.875rem;
    border: none;
    outline: none;
    padding: 1.125rem 4.5rem 1.125rem 1.25rem;
    background: var(--primary-color);
    color: var(--font-color);   
}

.region-selector::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 3rem;
    background: var(--primary-color);
    pointer-events: none;
}

.region-selector::after {
    content: url(/countries-of-the-world_fmchallenge/static/media/arrow.47dd3692.svg);
    position: absolute;
    right: 1.25rem;
    width: 0.625rem;
    top: 50%;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
    pointer-events: none;
}

.region-selector[data-theme="dark"]::after {
    content: url(/countries-of-the-world_fmchallenge/static/media/arrow-dark.2cd4a744.svg);
}
.country-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(16rem, 1fr));
    grid-gap: 3rem;
    gap: 3rem;
    background: var(--secondary-color);
}
.country-card {
    display: inline-block;
    font-family: "Nunito Sans";
    box-shadow: 0px 0px 4px 3px rgba(0, 0, 0, 0.02);
    border-radius: 0.25rem;
    overflow: hidden;
    background: var(--primary-color);
    min-width: 16rem;
    width: 100%;
}

.country-card img {
    width: 100%;
